export default {
	name: 'move-verify',
	data() {
		return {
			x: 0,
			oldx:0,
			isOk: false,
			size: {}
		};
	},
	mounted() {
		let getSize = (selector) => {
			return new Promise((resolve, reject) => {
				let view = uni.createSelectorQuery().in(this).select(selector);
				view.fields({
					size: true,
				}, (res) => {
					resolve(res.width);
				}).exec();
			});
		}
		this.$nextTick(() => {
			getSize("#pathway").then((res1) => {
				this.size.pathway = res1;
				getSize("#track").then((res2) => {
					this.size.track = res2;
				});
			})
		})
	},
	computed: {
		setOk() {
			return this.isOk
		},
		isX(){
			//console.log(this.oldx)
			// setTimeout(function(){
			// 	if(this.isOk){
			// 		this.oldx=0
			// 	}
			// },1500)
			return this.x
		},
		isOldx(){
			return this.oldx
		}
	},
	methods: {
		onMove(e) {
			this.oldx = e.detail.x;
			//console.log(this.oldx)
		},
		onEnd() {
			if ((this.oldx + 1) > (this.size.pathway - this.size.track)) {
				this.isOk = true;
					this.x = this.oldx;
					this.$nextTick(() => {
						setTimeout(() => {
							this.isOk=false;
							this.x = 0;
							this.oldx = 0;
						}, 1500);
					});
				this.$emit("result");
			} else {
				this.x = this.oldx;
				this.$nextTick(() => {
					setTimeout(() => {
						this.x = 0;
						this.oldx = 0;
					}, 100);
				});
			}
		}
	}
}
